
import Swiper from 'vux/src/components/swiper/swiper.vue'

import { banners } from '@/config/variables'
import { isBetweenDate } from "@/utils/object";
import myInfoMixin from '@/mixins/myInfoMixin'
import iconNormalMember from '@/assets/icon-normal-member.png'
import iconPlusMember from '@/assets/icon-plus-member.png'
export default {
components: {
Swiper
},
mixins: [myInfoMixin],
data () {
return {
banners: [],
iconNormalMember,
iconPlusMember
}
},
created () {
this.banners = banners.filter(v => {
if (!v.from || !v.to) {
return true
}
if (isBetweenDate(v.from, v.to)) {
return true
}
return false
})
}
}
